.line-through {
  text-decoration: line-through;
  user-select: none; }

.innerFooter {
  display: flex;
  flex-direction: column;
  text-align: center; }
  .innerFooter .footerRightColumn {
    order: -1; }

.footerLeftColumn p {
  font-size: 2em;
  font-family: Roboto Slab, serif;
  margin-bottom: 0;
  color: #949494; }

:global .inner-footer ul {
  list-style: none; }
  :global .inner-footer ul li {
    font-size: 1.4em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0; }
  :global .inner-footer ul a {
    display: block;
    color: #858585;
    text-decoration: none; }

@media screen and (max-width: 767px) {
  :global .inner-footer ul a {
    text-decoration: none; } }

@media screen and (min-width: 767px) {
  footer {
    display: grid;
    margin: 0 -9999rem;
    background: #FAFAFA;
    border-top: 1px solid #E5E5E5; }
  :global .inner-footer {
    display: grid;
    grid-template-columns: 20% 80%;
    grid-gap: 0;
    min-height: 70px; }
    :global .inner-footer ul a {
      display: inline-block; }
    :global .inner-footer ul a + a {
      margin-left: 20px; }
  .innerFooter {
    display: grid;
    text-align: initial; }
    .innerFooter .footerRightColumn {
      order: initial; }
  .innerFooter {
    display: grid;
    text-align: initial; }
  .footerLeftColumn {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .footerRightColumn {
    display: flex;
    align-items: center;
    justify-content: flex-end; } }
