.line-through {
  text-decoration: line-through;
  user-select: none; }

.header {
  display: inline-block;
  background: repeat-x bottom url("/app/assets/graphics/HEADER_TEXTURE.png");
  text-align: center;
  border-top: 1px solid #E5E5E5;
  border-bottom: 1px solid #E5E5E5; }

@media screen and (min-width: 767px) {
  .header {
    display: block;
    margin: 0 -9999rem;
    text-align: initial;
    border-top: initial;
    border-bottom: initial; } }

.headerRightColumn h2 {
  font-size: 4.5em;
  font-family: Roboto Slab, serif;
  margin-bottom: 0.55556em;
  color: #FF0000;
  line-height: 1.2em; }

.headerRightColumn p {
  font-size: 2.2em;
  font-family: Roboto Slab, serif;
  margin-bottom: 1.13636em;
  color: #6B6A6A;
  line-height: 1.3em; }

@media screen and (min-width: 767px) {
  .innerHeader {
    display: grid;
    grid-template-columns: 38.197% 61.803%;
    grid-gap: 0;
    min-height: 264px; }
  .headerLeftColumn {
    display: flex;
    align-items: center;
    justify-content: center; }
  .headerRightColumn {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column; }
    .headerRightColumn h2 {
      font-size: 4.5em;
      font-family: Roboto Slab, serif;
      margin-bottom: 0.27778em;
      line-height: 1em; }
    .headerRightColumn p {
      font-size: 2.2em;
      font-family: Roboto Slab, serif;
      margin-bottom: 0;
      line-height: 1.2em; } }
