.line-through {
  text-decoration: line-through;
  user-select: none; }

nav {
  display: grid;
  margin: 0 -9999rem;
  background: #FAFAFA;
  min-height: 70px;
  border-bottom: 1px solid #E5E5E5; }
  nav svg {
    opacity: 0.8; }

.innerNav {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 0; }

:global .inner-nav {
  background: transparent; }

.navLeftColumn h1 {
  font-size: 2em;
  font-family: Roboto Slab, serif;
  margin-bottom: 0;
  color: #FF001F; }

:global .display-menu {
  display: block !important; }

.navRightColumn {
  display: flex;
  align-items: center;
  justify-content: flex-end; }
  .navRightColumn label {
    color: transparent;
    font-size: 0;
    width: 33px;
    height: 33px;
    background: no-repeat center center url("/app/assets/graphics/burger_menu.svg");
    background-size: contain;
    cursor: pointer; }
  .navRightColumn input[type=checkbox] {
    position: absolute;
    top: -9999px;
    left: -9999px; }

.navLeftColumn {
  display: flex;
  align-items: center;
  justify-content: flex-start; }

@media screen and (max-width: 767px) {
  .closeMenu {
    background: no-repeat center center url("/app/assets/graphics/close_menu.png");
    width: 30px;
    height: 30px;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1;
    cursor: pointer;
    display: none; }
  .navRightColumn input[type=checkbox] ~ ul {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 62px 0;
    text-align: center;
    background: #515151;
    display: none; }
    .navRightColumn input[type=checkbox] ~ ul li {
      font-size: 1.8em;
      font-family: Roboto Mono, monospace;
      margin-bottom: 0.69444em;
      font-weight: 300; }
    .navRightColumn input[type=checkbox] ~ ul a:link {
      color: white;
      text-decoration: none; }
    .navRightColumn input[type=checkbox] ~ ul a:visited {
      color: white; }
    .navRightColumn input[type=checkbox] ~ ul a:focus {
      color: white; }
    .navRightColumn input[type=checkbox] ~ ul a:hover {
      color: white;
      text-decoration: underline;
      -webkit-text-decoration-skip: edges;
      text-decoration-skip: edges;
      -webkit-text-decoration-color: white;
      text-decoration-color: white; }
    .navRightColumn input[type=checkbox] ~ ul a:active {
      color: white; } }

@media screen and (min-width: 767px) {
  .navLeftColumn h1 {
    font-size: 2em;
    font-family: Roboto Slab, serif;
    margin-bottom: 0;
    color: #FF001F; }
  .navRightColumn ul {
    list-style: none; }
    .navRightColumn ul li {
      font-size: 1.4em;
      font-family: Roboto Mono, monospace;
      margin-bottom: 0; }
    .navRightColumn ul a {
      display: inline-block; }
    .navRightColumn ul a + a {
      margin-left: 20px; }
  a:link {
    color: #636363;
    text-decoration: none; }
  a:visited {
    color: #636363; }
  a:focus {
    color: #636363; }
  a:hover {
    color: #636363;
    text-decoration: underline;
    -webkit-text-decoration-skip: edges;
    text-decoration-skip: edges;
    -webkit-text-decoration-color: #FF001F;
    text-decoration-color: #FF001F; }
  a:active {
    color: #636363; } }
